import request from '../utils/request';

export function commentGetAll() {
  return request({
    url: '/comment/all',
    method: 'post',
  });
}

export function commentGetMany(data) {
  return request({
    url: '/comment/many',
    method: 'post',
    data
  });
}

export function commentDelete({ id }) {
  return request({
    url: '/comment/delete/' + id,
    method: 'get',
  });
}

export function commentAudit({ id }) {
  return request({
    url: '/comment/audit/' + id,
    method: 'get',
  });
}

export function login(data) {
  return request({
    url: '/user/login',
    method: 'post',
    data,
  });
}
