import request from '../utils/request';

export const markdown2html = ({ body }) =>
  request({
    url: '/blog/markdown2html',
    method: 'post',
    data: { body },
  });

export function ppv_history(params, data) {
  return request({
    url: '/ppv/history',
    method: 'get',
    params,
    data,
  });
}
export function ppv_domestic(params, data) {
  return request({
    url: '/ppv/domestic',
    method: 'get',
    params,
    data,
  });
}
export function ppv_records(params, data) {
  return request({
    url: '/ppv/records',
    method: 'get',
    params,
    data,
  });
}
