<template>
  <div class="page">
    <SearchOptions v-model:keyword="searchOpt.keyword" @search="initBlogs">
      <template #default>
        <div class="opt-item">
          <label>类型：</label>
          <el-select v-model="searchOpt.type">
            <el-option :value="0" label="博文" />
            <el-option :value="1" label="日记" />
            <el-option :value="2" label="信息合集" />
          </el-select>
        </div>
        <div class="opt-item">
          <label>发布：</label>
          <el-switch @change="save(scope.row)" v-model="searchOpt.publish" />
        </div>
      </template>
    </SearchOptions>
    <div class="blogs" v-loading="loading">
      <CustomTable
        :datas="[]"
        :props="propMap"
        :total="pageInfo.total"
        @page-change="pageChange"
        @select="selectRow"
      >
        <template #default>
          <el-table :data="blogs" style="width: 100%">
            <el-table-column label="日期" prop="time" width="120">
              <template #default="scope">
                <span style="text-align: center">{{ parseTime(scope.row.time) }}</span>
              </template>
            </el-table-column>
            <el-table-column label="标题" prop="title" />
            <el-table-column label="分类" prop="category" width="120" />
            <el-table-column label="访问量" prop="visited" width="80">
              <template #default="scope">
                <span style="text-align: center">{{ scope.row.visited }}</span>
              </template>
            </el-table-column>
            <el-table-column label="置顶" prop="pin" width="80">
              <template #default="scope">
                <el-switch @change="save(scope.row)" size="small" @click.prevent="() => {}" v-model="scope.row.pin" />
              </template>
            </el-table-column>
            <el-table-column label="RSS可见" prop="visible" width="100">
              <template #default="scope">
                <el-switch @change="save(scope.row)" size="small" @click.prevent="() => {}" v-model="scope.row.visible" />
              </template>
            </el-table-column>
            <el-table-column label="可评论" prop="commentable" width="100">
              <template #default="scope">
                <el-switch @change="save(scope.row)" size="small" @click.prevent="() => {}" v-model="scope.row.commentable" />
              </template>
            </el-table-column>
            <el-table-column label="发布" prop="publish" width="80">
              <template #default="scope">
                <el-switch @change="save(scope.row)" size="small" @click.prevent="() => {}" v-model="scope.row.publish" />
              </template>
            </el-table-column>
            <el-table-column label="原创" prop="original" width="80">
              <template #default="scope">
                <el-switch @change="save(scope.row)" size="small" @click.prevent="() => {}" v-model="scope.row.original" />
              </template>
            </el-table-column>

            <el-table-column label="操作" prop="visited" width="180">
              <template #default="scope">
                <el-popconfirm title="是否删除?" @confirm="blogDel(scope.row.id)">
                  <template #reference>
                    <el-button size="small" type="danger">删除</el-button>
                  </template>
                </el-popconfirm>
                <el-button size="small" type="primary" @click="selectRow(scope.row)">编辑</el-button>
              </template>
            </el-table-column>
          </el-table>
        </template>
      </CustomTable>
      <transition name="fade">
        <Dialog v-if="dialogVisible" :title="activeBlog.title" @close="dialogVisible = false">
          <template #body>
            <el-form label-width="fit-content">
              <el-form-item label="标题">
                <el-input v-model="activeBlog.title" />
              </el-form-item>
              <el-form-item label="类型">
                <span>{{ detectType(activeBlog.type) }}</span>
              </el-form-item>
              <el-form-item label="分类">
                <el-input v-model="activeBlog.category" />
              </el-form-item>
              <el-form-item label="时间">
                <el-date-picker type="datetime" v-model="activeBlog.time" />
              </el-form-item>
              <el-form-item label="简介">
                <textarea
                  type="textarea"
                  style="width: 100%; height: 180px"
                  v-model="activeBlog.intro"
                ></textarea>
              </el-form-item>
              <el-form-item label="备注">
                <textarea
                  type="textarea"
                  style="width: 100%; height: 100px"
                  v-model="activeBlog.note"
                ></textarea>
              </el-form-item>
              <el-form-item label="更新">
                <el-date-picker type="datetime" v-model="activeBlog.update" />
              </el-form-item>
            </el-form>
          </template>
          <template #footer>
            <div class="btns">
              <wsh-button type="info" text="查看" @comfirm="preview" />
              <wsh-button type="success" showComfirm text="保存" @comfirm="save(activeBlog)" />
              <wsh-button type="info" text="编辑" @comfirm="blogEdite" />
              <wsh-button
                showComfirm
                type="danger"
                text="删除"
                @comfirm="blogDel(selectedBlogId)"
              />
            </div>
          </template>
        </Dialog>
      </transition>
    </div>
  </div>
</template>

<script>
import { blogGetAll, blogDelete, blogUpdate } from '@/apis/index.js';
import { PopTip } from '@/utils/tip';
import SearchOptions from '@/components/SearchOptions';

export default {
  name: 'Blogs',
  components: { SearchOptions },
  props: {
    propMap: {
      type: Array,
      default: () => {
        return [];
      },
    },
  },
  data() {
    return {
      blogs: [],
      dialogVisible: false,
      selectedBlogId: null,
      loading: false,
      pageInfo: {
        total: 0,
        page: 0,
        limit: 10,
      },
      searchOpt: {
        keyword: '',
        type: 0,
        publish: true,
      },
    };
  },
  computed: {
    activeBlog() {
      if (this.selectedBlogId == null) return null;
      else {
        let blog = this.blogs.find(b => b.id == this.selectedBlogId);
        return blog || {};
      }
    },
    blogTitle() {
      const blog = this.blogs.find(b => b.id === this.selectedBlogId);
      if (blog) return blog.title;
      else return '';
    },
    searchParams() {
      return {
        ...this.searchOpt,
        page: this.pageInfo.page,
        limit: this.pageInfo.limit,
      };
    },
  },
  beforeMount() {
    this.initBlogs(this.pageInfo.page);
  },
  watch: {
    searchOpt: {
      deep: true,
      handler(preVal, postVal) {
        this.initBlogs();
      },
    },
  },
  methods: {
    parseTime(time){
      const date = new Date(time);
      return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`
    },
    detectType(n) {
      let name;
      switch (n) {
        case 1:
          name = '日记';
          break;
        case 2:
          name = '周刊';
          break;
        case 3:
          name = '草稿';
          break;
        default:
          name = '博文';
      }
      return name;
    },
    onDialogClose() {
      this.dialogVisible = false;
      this.selectedBlogId = null;
    },
    pageChange(num) {
      this.pageInfo.page = num || 0;
      this.initBlogs();
    },
    initBlogs() {
      this.loading = true;
      this.blogs.length = 0;
      blogGetAll(this.searchParams)
        .then(response => {
          const { total, rows } = response.data.data;
          this.blogs.push(...rows);
          this.pageInfo.total = total;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    selectRow(row) {
      this.selectedBlogId = row.id;
      this.dialogVisible = true;
    },
    blogDel(id) {
      blogDelete({ id })
        .then(response => {
          if (response.data.status) {
            PopTip.success('删除成功！');
            this.initBlogs();
          } else PopTip.warning('删除失败！');
        })
        .catch(() => {
          PopTip.warning('删除失败！');
        })
        .finally(() => {
          this.dialogVisible = false;
        });
    },
    blogEdite() {
      this.dialogVisible = false;
      this.$router.push({ name: 'writting', params: { id: this.selectedBlogId, type: 'blog' } });
    },
    save(obj) {
      blogUpdate(obj).then(res => {
        if (res.data.status) {
          PopTip.success('修改成功！');
        } else {
          PopTip.warning('修改失败');
        }
      });
    },
    preview() {
      if (this.activeBlog.type == 0) window.location = '/blog/' + this.selectedBlogId;
      else window.location = '/all-about-gis/' + this.selectedBlogId;
    },
  },
};
</script>

<style scoped lang="scss">
@include fade-transition();
.page {
  display: flex;
  flex-direction: column;
  height: inherit;
}
.opts {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 50px;
  padding: 10px;
  color: #000;
  background-color: rgba(199, 226, 236, 0.8745098039);
  .opt-item {
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 5px;
    &.expand {
      flex: 1;
    }
    label {
      width: 5em;
      color: #636262;
    }
  }
}
.blogs {
  flex: 1;
  display: flex;
  width: 100%;
  overflow: auto;
  @include scroll-style();
}
