import request from "../utils/request";

export const draftCreate = (data) => request({
  url: '/draft/create',
  method: 'post',
  data
})

export const draftMany = (page, limit) => request({
  url: '/draft/many',
  method: 'post',
  data: {page, limit}
})


export const draftDelete = ({id}) => request({
  url: '/draft/delete/' + id,
  method: 'get',
})

export const draftGet = ({id}) => request({
  url: '/draft/get/' + id,
  method: 'get',
})


export const draftUpdate = (data) => request({
  url: '/draft/create',
  method: 'post',
  data
})
export const draftPublish = (id) => request({
  url: '/draft/publish/' + id,
  method: 'get',
})

