import request from "../utils/request";

export const blogGetAll =  ({page, limit, keyword, type, publish}) =>{
  return request({
    url: "/blog/many",
    method: "post",
    data: {page, limit, type, keyword, publish},
  });
}
export function blogUpdate(data) {
  return request({
    url: "/blog/update",
    method: "post",
    data,
  });
}

export function blogGetOne(id) {
  return request({
    url: "/blog/get/" + id,
  });
}

export function blogCreate(data) {
  return request({
    url: "/blog/create",
    method: "post",
    data: data,
  });
}

export function blogDelete({ id }) {
  return request({
    url: "/blog/delete/" + id,
  });
}

export function blogStasis({ type , params}) {
  return request({
    url: "/blog/stasis/" + type,
    params
  });
}
export const commentCreate = ({ body, blogId, nickName }) => {
  return request({
    url: "/comment/create",
    method: "post",
    data: {
      body,
      blogId,
      nickName,
    },
  });
};

